<template>
  <el-form :inline="true" size="small" :model="param" :rules="rules" ref="individualVehicleForm">
    <el-form-item label="机构代码" prop="subCompanyCode">
      <el-select placeholder="请选择机构代码" size="medium" v-model="param.subCompanyCode" :disabled="!lifeFormOptionShow">
        <el-option
          v-for="company in initData.companyList"
          :key="company.code"
          :label="company.name"
          :value="company.code">
        </el-option>
      </el-select>
    </el-form-item>
    <el-form-item label="渠道" prop="channel">
      <el-select placeholder="请选择渠道" size="medium" v-model="param.channel" :disabled="!lifeFormOptionShow">
        <el-option
          v-for="item in initData.channelList"
          :key="item.code"
          :label="item.name"
          :value="item.code">
        </el-option>
      </el-select>
    </el-form-item>
    <el-form-item label="保单范围" prop="rollback" v-show="rollbackFlag">
      <el-select placeholder="请选择回滚月份" size="medium" v-model="param.rollback">
        <el-option label="回滚12个月" value="12"></el-option>
        <el-option label="回滚15个月" value="15"></el-option>
        <el-option label="回滚18个月" value="18"></el-option>
        <el-option label="回滚21个月" value="21"></el-option>
        <el-option label="回滚24个月" value="24"></el-option>
        <el-option label="回滚27个月" value="27"></el-option>
        <el-option label="回滚30个月" value="30"></el-option>
        <el-option label="回滚33个月" value="33"></el-option>
        <el-option label="回滚36个月" value="36"></el-option>
      </el-select>
    </el-form-item>
    <el-form-item label="版本号" v-show="versionFlag">
      <el-input readonly v-model="param.version"></el-input>
    </el-form-item>
    <el-form-item>
      <el-button v-show="viewParameters" plain size="small" @click="querySector"  type="primary">查看参数</el-button>
    </el-form-item>
  </el-form>
</template>

<script>
import {mapState} from 'vuex'
import {initData} from '@/api/individual'

export default {
  name: 'QueryForm',
  data () {
    return {
      initData: {},
      rules: {
        subCompanyCode: [{required: true, trigger: 'change', message: '机构不能为空'}],
        channel: [{required: true, trigger: 'change', message: '渠道不能为空'}]
      }
    }
  },
  computed: mapState({
    viewParameters: state => state.individual.viewParameters,
    lifeFormOptionShow: state => state.individual.lifeFormOptionShow,
    rollbackFlag: state => state.individual.rollbackFlag,
    versionFlag: state => state.individual.versionFlag,
    param: state => state.individual.param
  }),
  created () {
    initData().then(response => {
      this.initData = response.data
    })
  },
  methods: {
    querySector () {
      this.valid('querySector')
    },
    valid (method) {
      this.$refs['individualVehicleForm'].validate(valid => {
        if (valid) {
          this.$emit(method)
        }
      })
    }
  }
}
</script>

<style scoped>
  .el-button {
    width: 98px;
    height: 36px;
    font-size: 14px;
  }
</style>



